.Footer {
  background-color: #46817a;
}

.Footer-container {
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-template-rows: 1fr 1fr;
  grid-template-areas: 
  "A1 B1"
  "A2 B2";
  padding: 5px;
}

.Footer-item-A1 {
  grid-area: A1;
}

.Footer-item-A2 {
  grid-area: A2;
}

.Footer-item-B1 {
  grid-area: B1;
}

.Footer-item-B2 {
  grid-area: B2;
}